import React, { useRef } from "react";

// reactstrap components
import { Button, Container, Row, Col } from "reactstrap";

class Hero extends React.Component {
  constructor(props) {
    super(props);

    this.punkContainer = React.createRef();
    this.state = {
      punks: [],
      width: window.innerWidth,
      height: window.innerHeight,
    }
  }
  fillPunks() {
    var lastLeftPos = -150;
    do {
      let punks = this.state.punks;
      lastLeftPos = punks.length ? punks[punks.length - 1][1] : -150;
      punks.push([Math.ceil(Math.random() * 10000), lastLeftPos + 150]);
      this.setState({ punks: punks });
    } while (lastLeftPos < this.state.width)
  }
  resetPunks() {
    this.fillPunks();

    let steps = 0;

    const interval = setInterval(() => {
      steps++;
      let punks = this.state.punks;
      punks = punks.map(function(element, index) {
        return [element[0], element[1] - 1];
      });

      if (steps % 150 === 0) {
        punks.shift();
        this.fillPunks();
      } else {
        this.setState({ punks: punks });
      }
    }, 24);
  }
  updateDimensions = () => {
    this.setState({ width: window.innerWidth, height: window.innerHeight });
    this.fillPunks();
  };
  componentDidMount () {
    window.addEventListener('resize', this.updateDimensions);
    this.resetPunks();
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.updateDimensions);
  }
  render() {
    return (
      <>
        <div className="position-relative">
          <section className="section section-hero section-shaped" style={{backgroundColor: '#FFF'}}>
            <Container className="shape-container d-flex align-items-center py-0">
              <blockquote className="blockquote text-center">
                <a href="https://twitter.com/bitfalls" target="_blank">
                  <img src="https://cdn.substack.com/image/fetch/f_auto,q_auto:good,fl_progressive:steep/https%3A%2F%2Fbucketeer-e05bbc84-baa3-437e-9518-adb32be77984.s3.amazonaws.com%2Fpublic%2Fimages%2F0eddf503-ff8a-42c7-9487-44ce24045f3b_400x400.jpeg" alt="Circle image" className="img-fluid rounded-circle shadow" style={{width: 150}} />
                </a>

                <p className="mb-0" style={{fontSize: 20, fontWeight: 'bold'}}>Kanaria is the first functional NFT collection that is Kusama native, but the true pioneers in the space are Substrapunks.</p>
                <footer className="blockquote-footer"><a href="https://twitter.com/bitfalls" target="_blank" style={{color: 'inherit'}}>Bruno the chad</a>
                </footer>
              </blockquote>
            </Container>
            <div className="py-5 punk-scroller" ref={this.punkContainer}>
              <div className="punk-scroller-contents">
                { this.state.punks.map((item,i) => (
                    <div className="punk" style={{left: item[1]}} key={i}>
                      <a href={`https://unqnft.io/#/market/token-details?collectionId=23&tokenId=${item[0]}`} target="_blank">
                        <img src={`https://ipfs-gateway.usetech.com/ipns/QmaMtDqE9nhMX9RQLTpaCboqg7bqkb6Gi67iCKMe8NDpCE/images/punks/image${item[0]}.png`} />
                      </a>
                    </div>
                ))}
              </div>
            </div>
            <Container className="shape-container d-flex align-items-center py-0">
              <div className="col px-0">
                <Row className="align-items-center justify-content-center">
                  <Col className="text-center mt-4" lg="8">
                    <h1 className="display-4">Dear fellow substrate NFTers, </h1>
                    <p className="lead">
                      Back in April, Substrapunk holders received a very generous <a href="https://app.subsocial.network/@rmrkapp/kanaria-s-punkdrop-a-gift-to-the-substrapunks-community-10215" target="_blank">airdrop of Kanaria eggs</a>. Since then, lots of exciting stuff has happened in the world of Substrate / Kusama NFTs, including the launch of both Kanaria and Unique's marketplaces. We have been eager to give something back to the Kanaria community for a while and that time has finally come. We are happy to announce a community-led punk raffle in which all Kanaria holders will have a chance to win one of 50 Substrapunks, with a grand prize of one special punk (TBA). The procedure is as follows:<br/><br/>

                      <div style={{textAlign: 'left', margin: '0px auto', maxWidth: 460, fontWeight: 'bold', fontSize: 17}}>
                      <ol>
                        <li>Hold a Kanaria bird</li>
                        <li>Claim a raffle ticket above</li>
                        <li>Hold your raffle ticket on the draw date (TBA)</li>
                        <li>Watch the live stream to see if you win</li>
                      </ol>
                      </div>

                      <br/>
                      Good luck!
                      <br/>-<br/>
                      <strong>Substrapunk Community</strong>
                      <br/>
                      <span style={{fontSize: 15}}><a href="https://t.me/substranft" target="_blank"><i
                          className="ni ni-send"></i> Please join us on Telegram!</a></span>
                    </p>
                    <div className="btn-wrapper mt-5">
                      <Button
                        className="btn-white btn-icon mb-3 mb-sm-0"
                        color="default"
                        href="https://substrapunk.medium.com/substrapunk-introduction-cfc4eb74e9a"
                        size="lg"
                        target="_blank"
                      >
                        <span className="btn-inner--icon mr-1">
                          <i className="ni ni-hat-3" />
                        </span>
                        <span className="btn-inner--text">Learn more about Substrapunks</span>
                      </Button>
                    </div>
                  </Col>
                </Row>
              </div>
            </Container>
            {/* SVG separator */}
            <div className="separator separator-bottom separator-skew zindex-100">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                preserveAspectRatio="none"
                version="1.1"
                viewBox="0 0 2560 100"
                x="0"
                y="0"
              >
                <polygon
                  className="fill-white"
                  points="2560 0 2560 100 0 100"
                />
              </svg>
            </div>
          </section>
        </div>
      </>
    );
  }
}

export default Hero;
