import React, { Suspense } from "react";
import { web3Accounts, web3Enable, web3AccountsSubscribe } from '@polkadot/extension-dapp';
import { encodeAddress } from '@polkadot/util-crypto';
import PunkRaffle from 'assets/PunkRaffle.glb';
import PunkRaffleLight from 'assets/PunkRaffle_l.glb';
import { Vector3, Color3, Color4, ActionManager, SetValueAction } from '@babylonjs/core';
import '@babylonjs/loaders/glTF';
import { Canvas } from "react-three-fiber";
import { OrbitControls, useGLTF } from "@react-three/drei";

// reactstrap components
import { Button, Container, Row, Col, FormGroup, UncontrolledCarousel } from "reactstrap";

//const headerImage = require("assets/img/brand/headerbg.png");
//const headerImageRain = require("assets/img/brand/let-it-rain.jpg");
const headerImage = require("assets/img/brand/header3.png");
const raffleTicketImage = require("assets/img/brand/RaffleTicket.png");

function Model(props) {
  const { scene } = useGLTF(PunkRaffleLight);
  return <primitive object={scene} />;
}

class Carousel extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      connectButton: "Connect wallet",
      claimStatus: 0,
      accounts: [],
      claimAddress: '',
      avocadoYPos: -1.5,
      avocadoScaling: 5.0
    }
  }
  componentDidMount () {

  }
  onModelLoaded(model) {
    let mesh = model.meshes[1];
    mesh.actionManager = new ActionManager(mesh._scene);
    mesh.actionManager.registerAction(
        new SetValueAction(
            ActionManager.OnPointerOverTrigger,
            mesh.material,
            'wireframe',
            true
        )
    )
    mesh.actionManager.registerAction(
        new SetValueAction(
            ActionManager.OnPointerOutTrigger,
            mesh.material,
            'wireframe',
            false
        )
    )
  }
  checkForBirds() {
    web3Enable('PunkRaffle').then(extensions => {
      this.setState({ connectButton: "Connecting..." });

      if (extensions.length === 0) {
        //this.setState({ birdStatus: "Please install the <a href='https://polkadot.js.org/extension/' target='_blank'>Polkadot extension</a>" });
            this.setState({ connectButton: "Unable to access the Polkadot extension" });
        return;
      }

      let unsubscribe; // this is the function of type `() => void` that should be called to unsubscribe

      web3Accounts().then(accounts => {
        accounts = accounts.map(function(account) {
          account.address = encodeAddress(account.address, 2);
          return account;
        });

        Promise.all(accounts.map(account =>
            fetch('https://substrapunk.onrender.com/raffle/' + account.address).then(resp => resp.json())
        )).then(results => {
          let birdAccounts = accounts.filter(function(account) {
            for (let i = 0; i < results.length; i++) {
              if (results[i].address == account.address && results[i].has_bird && !results[i].submitted_raffle) {
                return true;
              }
            }
            return false;
          });

          let claims = accounts.filter(function(account) {
            for (let i = 0; i < results.length; i++) {
              if (results[i].address == account.address && results[i].submitted_raffle) {
                return true;
              }
            }
            return false;
          });

          if (birdAccounts.length > 0) {
            this.setState({ claimAddress: birdAccounts[0].address });
            this.setState({
              connectButton: "Claim ticket!",
              claimButton: "Claim ticket!",
              claimStatus: 1,
              accounts: birdAccounts,
            });
          } else if(claims.length > 0) {
            this.setState({
              claimStatus: 4,
            });
          } else {
            this.setState({
              claimStatus: 2,
              claimButton: "Get some!",
              accounts: [],
            });
          }
        })
      });
    });
  }
  claimTicket() {
    this.setState({ claimButton: "Processing..." });
    fetch('https://substrapunk.onrender.com/raffle/' + this.state.claimAddress + '/submit', { method: 'POST' })
        .then(response => response.json())
        .then(data => {
          this.setState({
            claimStatus: 3,
          });
        });
  }
  render() {
    return (
      <>
        <div className="text-center" id="big-header" style={{backgroundImage: 'url(' + headerImage + ')'}}>
        {/*<div className="text-center" style={{width: '100%'}}>*/}
          {/*<img src={headerImageRain} className="img-fluid" style={{width: '100%', maxWidth: '1024px'}}/>*/}
          {/*<Container>*/}
            {/*<Row className="align-items-center mb-lg-5 text-left">*/}
            {/*  <Col className="mt-6 mb-4 mb-lg-0" lg="7" md="9">*/}
            {/*    <h1 style={{color: "#FFF", fontSize: 50, fontWeight: "bold"}}>PUNK DROP</h1>*/}
            {/*    <h2 style={{color: "#FFF"}}>For kanaria holders</h2>*/}
            {/*  </Col>*/}
            {/*  <Col className="mb-lg-auto mb-md-auto d-none d-md-block d-lg-block text-right" lg="5" md="3" style={{position: "relative"}}>*/}
            {/*    <img src={kanariaImage} style={{position: "absolute", height: 340, top: -40, right: 0}} />*/}
            {/*  </Col>*/}
            {/*</Row>*/}
          {/*</Container>*/}
        </div>
        <section className="section mt-lg-6">
          <div className="shape shape-style-1 shape-default">
            <span />
            <span />
            <span />
            <span />
            <span />
            <span />
          </div>
          <Container>
            <Row className="align-items-center mb-lg-5">
              <Col className="mb-lg-auto mb-md-auto d-none d-md-block d-lg-block text-right mt-md-5  mt-lg-0" lg="5" md="6" style={{position: "relative"}}>
                <div className="text-right" style={{position: "absolute", height: 650, top: -120, left: 20}}>
                  <Canvas linear camera={{ position: [0, 18, 25], fov: 20 }}>
                    {/*<ambientLight intensity={35} />*/}
                    <pointLight position={[-25, 10, 10]} intensity={1} />
                    <pointLight position={[-5, 10, 10]} intensity={1} />
                    <pointLight position={[5, 5, 15]} intensity={0.5} />
                    <pointLight position={[20, 10, 10]} intensity={0.55} />
                    <pointLight position={[35, 10, 10]} intensity={0.3} />
                    {/*<directionalLight position={[0, 18, 25]} intensity={0.6} penumbra={1}/>*/}
                    {/*<directionalLight position={[0, 18, -75]} intensity={0.6} penumbra={1}/>*/}
                    {/*<hemisphereLight position={[0, 18, 0]} intensity={0.6}/>*/}
                    {/*<spotLight distance={1} intensity={0.6}/>*/}
                    <Suspense fallback={null}>
                      <Model />
                    </Suspense>
                    <OrbitControls enableZoom={false} autoRotate={true} autoRotateSpeed={2.5} />
                  </Canvas>
                </div>
              </Col>
              <Col className="mb-6 mb-lg-0" lg="7" md="6">
                {this.state.claimStatus === 0 &&
                  <div>
                    <h1 className="text-white font-weight-light">
                      Got a bird?
                    </h1>
                    <p className="lead text-white mt-4">
                    Claim your raffle ticket for a Substrapunk now! Connect your Polkadot wallet and see if you're eligible.
                    </p>
                    <Button className="btn-white mt-4" color="default" onClick={this.checkForBirds.bind(this)}>
                      {this.state.connectButton}
                    </Button>
                  </div>
                }
                {this.state.claimStatus === 1 &&
                <div>
                  <h1 className="text-white font-weight-light">
                    You are eligible!
                  </h1>
                  <p className="lead text-white mt-4">
                    Select the wallet on which you would like to receive your raffle ticket.
                  </p>
                  <FormGroup>
                    <select style={{maxWidth: '100%'}} value={this.state.claimAddress} onChange={(e) => this.setState({ claimAddress: e.target.value })}>
                      { this.state.accounts.map((account,i) => (
                          <option key={i} value={account.address}>{account.meta.name} ({account.address})</option>
                      ))}
                    </select>
                  </FormGroup>
                  <Button className="btn-white mt-4" color="default" onClick={this.claimTicket.bind(this)}>
                    {this.state.claimButton}
                  </Button>
                </div>
                }
                {this.state.claimStatus === 2 &&
                <div>
                  <h1 className="text-white font-weight-light">
                    You don't seem to own any birds.
                  </h1>
                  <p className="lead text-white mt-4">
                    You can always get some at the marketplace however.
                  </p>
                  <Button className="btn-white mt-4" color="default" onClick={() => window.open('https://kanaria.rmrk.app/catalogue?nftType=birds&page=1&sortBy=price', '_blank')}>
                    {this.state.claimButton}
                  </Button>
                </div>
                }
                {this.state.claimStatus === 3 &&
                <div>
                  <h1 className="text-white font-weight-light">
                    Claim successful
                  </h1>
                  <p className="lead text-white mt-4">
                    You will receive your raffle ticket NFT in <a href={"https://singular.rmrk.app/space/" + this.state.claimAddress} target="_blank" style={{color: "#FFF", fontWeight: "bold"}}>your Singular account</a> soon!
                  </p>
                </div>
                }
                {this.state.claimStatus === 4 &&
                <div>
                  <h1 className="text-white font-weight-light">
                    Ticket already claimed
                  </h1>
                  <p className="lead text-white mt-4 mb-6">
                    It seems that you've already claimed your raffle ticket. The raffle ticket NFT will be sent to <a href={"https://singular.rmrk.app/space/" + this.state.claimAddress} target="_blank" style={{color: "#FFF", fontWeight: "bold"}}>your Singular account</a> soon.
                  </p>
                </div>
                }
              </Col>
            </Row>
          </Container>
          {/* SVG separator */}
          <div className="separator separator-bottom separator-skew">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              preserveAspectRatio="none"
              version="1.1"
              viewBox="0 0 2560 100"
              x="0"
              y="0"
            >
              <polygon className="fill-white" points="2560 0 2560 100 0 100" />
            </svg>
          </div>
        </section>
      </>
    );
  }
}

export default Carousel;
